<!--
  @auth:yw
  echarts柱图排行+同比
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from 'echarts';

  export default {
    name: "HorizontalBar",
    props: {
      animateFlag: Boolean,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "100%",
              height: "100%"
            },
            options: {
              rollFlag: false, //tootil是否自动轮播默认不是自动轮播
            },
            data: [
              {
                "name": "石家庄市",
                "value": 1067,
              },
              {
                "name": "唐山市",
                "value": 47
              },
              {
                "name": "秦皇岛市",
                "value": 11
              },
              {
                "name": "邯郸市",
                "value": 7
              },
              {
                "name": "邢台市",
                "value": 197
              },
              {
                "name": "保定市",
                "value": 371
              },
              {
                "name": "张家口市",
                "value": 134
              },
              {
                "name": "承德市",
                "value": 156
              },
              {
                "name": "沧州市",
                "value": 30
              },
              {
                "name": "廊坊市",
                "value": 210
              },
              {
                "name": "衡水市",
                "value": 67
              }
            ]
          };
        }
      }
    },
    data() {
      return {
        myChart: null,
        rollFlag: false
      };
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;
        this.myChart = echarts.init(this.$refs.chartEl);
        let propData = JSON.parse(JSON.stringify(this.params.data));
        //数据处理
        let propDataBg = [], scatterArr = [], dataValue = [], dataName = [], dataMax = 0, total = 0;

        propData.forEach(item => {
          total += item.value;
        });
        // 同比
        let tbData = [];
        propData.forEach(item => {
          tbData.push(item.tb);
        });
        
        // 数据全为0时，数据顺序和初始顺序反转（排序失效）
        if (total === 0) {
          propData = [...propData].reverse();
        }

        propData.forEach(item => {
          dataName.push(item.name);
          dataValue.push(item.value);
          dataMax = (item.value > dataMax ? item.value : dataMax);
          total += item.value;
        });

        propData.forEach(item => {
          let obj1 = {
            "name": item.name,
            "value": dataMax * 1.1,
            itemStyle: {
              borderColor: "#117cbb",
            },
          };
          propDataBg.push(obj1);

          let obj2 = {
            "name": item.name,
            "value": 0
          };
          scatterArr.push(obj2);
        });

        // 默认高亮
        let highlightIndex = this.params.options && this.params.options.highlightIndex;
        if (highlightIndex || highlightIndex === 0) {
          propData[highlightIndex].itemStyle = {
            color: '#f5e752'
          };
          propDataBg[highlightIndex].itemStyle = {
            borderColor: '#f5e752'
          };
          scatterArr[highlightIndex].itemStyle = {
            color: '#f5e752'
          };
        }
        this.option = {
          grid: {
            left: '16%',
            top: '0%',
            right: '0%',
            bottom: '0%',
            containLabel: true
          },
          xAxis: [
            {
              show: false,
            }
          ],
          yAxis: [
            {
              axisTick: 'none',
              axisLine: 'none',
              offset: '20',
              axisLabel: {
                color: '#fff',
                fontSize: 16,
                formatter: (params) => {
                  // 标签文字长度超过4时，...显示
                  if (params.length > 4) {
                    return params.slice(0, 4) + '...'
                  } else {
                    return params
                  }
                }
              },
              data: dataName
            },
            {
              axisTick: 'none',
              axisLine: 'none',
              axisLabel: {
                rich: {
                  color1: {
                    color: '#ffffff',
                    fontSize: '16',
                    width: 20,
                    align: 'right',
                  },
                  color2: {
                    color: '#ffffff',
                    fontSize: '16',
                    align: 'right',
                    padding: [0, 0, 0, 18]
                  },
                },
                formatter:(params) => {
                  return '{color1|' + params + '}{color2|同}'
                }
              },
              data: dataValue
            },
            {
              axisTick: 'none',
              axisLine: 'none',
              axisLabel: {
                padding: [0, 0,0 ,70],
                textStyle: {
                  fontSize: '16',
                  color: function (value, index) {
                      return value >= 0 ?  '#e54633' : '#a9ee50' ;
                  },
                },
                formatter:(params) => {
                  let str = (params * 100).toFixed();
                  // return  str * 100 + '%'
                  return  (str > 0 ? "+" + str : str)  + '%'
                }
              },
              data: tbData
            },
            {
              name: '',
              nameGap: '50',
              nameTextStyle: {
                color: '#ffffff',
                fontSize: '16',
              },
              axisLine: {
                lineStyle: {
                  color: 'rgba(0,0,0,0)'
                }
              },
              data: [],
            }
          ],
          series: [
            {
              name: '条',
              type: 'bar',
              yAxisIndex: 0,
              data: propData,
              label: {
                normal: {
                  show: false,
                  position: 'right',
                  textStyle: {
                    color: '#ffffff',
                    fontSize: '16',
                  }
                },
                
              },
              barWidth: 8,
              itemStyle: {
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                  offset: 0,
                  color: '#4cbfef'
                }, {
                  offset: 1,
                  color: '#fdaa33'
                }]),
                barBorderRadius: 20
              },
              z: 2
            },
            {
              name: 'bar背景',
              type: 'bar',
              yAxisIndex: 1,
              barGap: '-100%',
              barWidth: 12,
              itemStyle: {
                color: 'transparent',
                borderWidth: 2,
                barBorderRadius: 5,
              },
              data: propDataBg,
              z: 1
            },
            {
              name: '圆点',
              type: 'scatter',
              hoverAnimation: false,
              data: scatterArr,
              yAxisIndex: 2,
              symbolSize: 24,
              itemStyle: {
                color: '#4cbfef',
                opacity: 1,
              },
              z: 2
            }
          ],
          // tooltip: {
          //   trigger: 'axis',
          //   axisPointer: {
          //     type: 'shadow'
          //   }
          // },
        };
        this.myChart.setOption(this.option);

        //柱图点击事件
        let curName = '';
        this.myChart.on("click", params => {
          if (this.params.options && this.params.options.click) {
            curName = (curName === params.name ? '' : params.name);

            this.option.series[0].data.forEach(el => {
              el.itemStyle = {};
              if (el.name === curName) {
                el.itemStyle.color = "#f5e752"
              } else {
                el.itemStyle.color = new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                  offset: 0,
                  color: '#4cbfef'
                }, {
                  offset: 1,
                  color: '#fdaa33'
                }])
              }
            });

            this.option.series[1].data.forEach(el => {
              el.itemStyle = {};
              if (el.name === curName) {
                el.itemStyle.borderColor = '#f5e752'
              } else {
                el.itemStyle.borderColor = '#117cbb'
              }
            });

            this.option.series[2].data.forEach(el => {
              el.itemStyle = {};
              if (el.name === curName) {
                el.itemStyle.color = '#f5e752'
              } else {
                el.itemStyle.color = '#4cbfef'
              }
            });

            this.myChart.setOption(this.option);
            this.$emit("departmentClick", curName);
          }
        });
      },
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      },
      realData() {
        return this.params.data
      }
    },
    watch: {
      realData: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

<style lang="stylus" scoped>
</style>
