<template>
	<div class="wrapper">
		<div class="content">
			<div class="w-left">
				<ctitle :title="'二级内容分类信访量'" class="w-l-title"></ctitle>
				<div class="echarts">
					<horizontal-bar :params="xfbmBarParams"></horizontal-bar>
				</div>
			</div>
			<div class="w-right">
				<ctitle :title="'各级内容分类占比'" class="w-l-title"></ctitle>
				<div class="echarts">
					<DiskPie :params='nrflzbfbParams'></DiskPie>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import {get_http} from "@/api/index";
import horizontalBar from "@/components/bar/HorizontalBar1";
import ctitle from "@/components/title/index";
import DiskPie from "@/components/pie/DiskPie";
export default {
	name: 'wtfl',
	components: {
		horizontalBar,
		ctitle,
		DiskPie
	},
	data() {
		return {
			// 1.二级内容分类信访量
			xfbmBarParams: {
				data: [],
				canvasStyle: {
					width: "100%",
					height: "100%"
				}
			},
			// 2.各级内容分类占比分布
			nrflzbfbParams: {
				data: [],
				canvasStyle: {
					width: "100%",
					height: "100%"
				}
			}
		}
	},
	mounted() {
		this.getAPI(this.year)
	},
	methods: {
		getAPI(year) {
			// 1.二级内容分类信访量
			get_http("xftstjfxNrflDyfbNrflxfl", {year: year})
			.then(res => {
				this.xfbmBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
			})
			.catch(err => {
				console.log(err);
			});
			// 2.各级内容分类占比分布
			get_http("xftstjfxNrflDyfbGjnrfl", {year: year})
			.then(res => {
				this.nrflzbfbParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
			})
			.catch(err => {
				console.log(err);
			});
		}
	},
	computed: {
		year() {
			return this.$store.state.year;
		}
	},
	watch: {
		year(val) {
			this.getAPI(val);
		}
	}
}
</script>
<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.8rem
	height calc(100% - 0.8rem)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		display flex
		.w-left
			width 5.5rem
			height 100%
			position relative
			.echarts
				height calc(100% - 0.28rem)
				box-sizing border-box
				border-right 1px #91e3f7 dashed
		.w-right
			flex 1
			.echarts
				width 100%
				height 100%
</style>