<!--
echarts 基本饼图
@auth:lyq
-->
<template>
  <div
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width:canvasStyle.width,height:canvasStyle.height}"
  ></div>
</template>

<script>
  import echarts from 'echarts'

  export default {
    name: "PieProgress",
    props: {
      params: {
        type: Object,
        default: function () {
          return {
            canvasStyle: {
              width: "500px",
              height: "500px"
            },
            options: {
            },
          };
        }
      },
    },
    data() {
      return {
				myChart: null,
				data: [
				
				]
      };
    },
    mounted() {
      this.initChart();
      // this.$once('hook:beforeDestroy', () => {
      //   if (this.interval) {
      //     clearInterval(this.interval);
      //   }
      //   this.myChart && this.myChart.dispose();
      // })
    },
    methods: {
      initChart() {
        // if (this.myChart) {
        //   this.myChart.dispose();
        // }
				 this.myChart = echarts.init(this.$refs.chartEl);
				 let params = this.params
				let option = {
          // tooltip: {
          //   trigger: 'item',
          //   formatter: '{c}%'
          // },
					series: {
							type: 'sunburst',
							highlightPolicy: 'ancestor',
							data: params.data,
							radius: [0, '100%'],
              sort: null,
              
							levels: [{}, {
									r0: '15%',
									r: '30%',
									itemStyle: {
											borderWidth: 4,
											borderColor: '#050e27'

									},
									label: {
											rotate: 'tangential',
											fontSize: 20
									}
							}, {
									r0: '33%',
									r: '77%',
									itemStyle: {
											borderWidth: 2,
											borderColor: '#050e27'

									},
									label: {
											align: 'right',
											fontSize: 15,
											fontWeight: 'bold'
									}
							}, {
									r0: '80%',
									r: '82%',
									label: {
											position: 'outside',
											silent: false,
											fontSize: 18,
											color: '#cbe2fb',
											rotate: 30
									},
									itemStyle: {
											borderWidth: 3,
											borderColor: '#050e27'
									}
							}]
					}
 				};
      	this.myChart.setOption(option);
      }
    },
    computed: {
      canvasStyle() {
        return {
          width: this.params.canvasStyle
            ? this.params.canvasStyle.width
            : "500px",
          height: this.params.canvasStyle
            ? this.params.canvasStyle.height
            : "500px"
        };
      },
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.initChart();
        },
        deep: true
      }
    }
  };
</script>

<style scoped>
</style>
